* {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  text-decoration: none;
  color: white;
  font-family: 'Berkshire Swash', cursive;
}
.App {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  height: 100vh;
  width: 100vw;
}

.mainContainer {
  padding: 25px 0
}


.wrapper {
  width: 95vw;
  height: auto;
  margin: 30px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider {
  width: 95vw;
  position: relative;
  box-shadow: 0 10px 20px -10px rgba(0, 0, 0, 0.2);
  display: flex;
  overflow: hidden;
}

.slide {
  height: 100px;
  display: flex;
  align-items: center;
  animation: slideshow 20s linear infinite;
}

.slide img {
  height: 70px;
  padding: 0 30px 0 30px;
}

@keyframes slideshow {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-100%);
  }
}

.card {
  position: relative;
  backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.074);
  border: 1px solid rgba(255, 255, 255, 0.222);
  width: 17vw;
  height: 60vh;
  margin: 15px;
  transition: all 1s;
  border-radius: .5rem;
}

.cardfalse:hover {
  transition: all .5s;
  scale: 1.05;
  box-shadow: 0px 0px 5px 0px rgb(106, 106, 106);
}

.cardtrue {
  width: 100vw;
  height: 100vh;
  margin: 0;
  border: none;
  border-radius: 0;
}
